body {
    background-color: #43314d;
    padding: 20px;
    margin: 20;
    
  }
  h1, h2, p, ul, li {
    font-family: monospace;
  }
  ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  ul.header {
    background-color: #805D93;
    padding: 0;
  }
  ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #533c5f;

    justify-content: center;
    align-items: center;
    padding: 20px;
    font-family: monospace;
    color: #FFF;
  }
  
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
    
  }

  .container-div{ 
   /* //Display: flex; */
    Height: 10%;
    Width: 10%; 
    align-content: center;
    justify-content: center;
    }

    .fa {
        padding: 20px;
        font-size: 30px;
        width: 30px;
        text-align: center;
        text-decoration: none;
        margin: 5px 2px;
        border-radius: 50%;
      }
      
      .fa:hover {
          opacity: 0.7;
      }
      
      .fa-facebook {
        background: #3B5998;
        color: white;
      }
      
